import React from 'react';
// Redux
import { useSelector } from 'react-redux';
// MUI Components
import { Box, Grid, Paper, Typography } from '@mui/material'
import { makeStyles } from '@mui/styles'


const useStyles = makeStyles({
    rowsTitleFirst: {
        border: '1px solid #000',
        fontWeight: 'bolder',
        textAlign: 'center',
    },
    rowsContentFirst: {
        border: '1px solid #000',
        padding: '5px',
        paddingLeft: '15px'
    },
    rowsTitle: {
        border: '1px solid #000',
        borderLeft: 'none',
        fontWeight: 'bolder',
        textAlign: 'center',
    },
    rowsContent: {
        border: '1px solid #000',
        borderLeft: 'none',
        borderTop: 'none',
        padding: '5px',
        paddingLeft: '15px',
        textAlign: 'center',

    },
})

export default function ComakersDetailsPage(props) {

    const loanRedux = useSelector((state) => state.loans.value);

    const selectedLoan = loanRedux.selectedLoan;
    const classes = useStyles(props);

    const comakers = selectedLoan.data.comaker;

    return (
        <div>
            <Box
                display="flex"
                flexDirection="row"
                justifyContent="center"
                sx={{
                    borderTopLeftRadius: 10,
                    borderTopRightRadius: 10,
                    border: '1px solid #00BFA5',
                    backgroundColor: "#00BFA5",
                    color: "#FFF",
                    width: 150,
                    height: 35,
                    p: 1,
                }}
            >
                <Typography>
                    Co-Maker Details
                </Typography>
            </Box>
            <Paper sx={{ p: 1 }}>
                <Grid container>
                    <Grid item xs={5} className={classes.rowsTitleFirst}>
                        Full Name
                    </Grid>
                    <Grid item xs={2} className={classes.rowsTitle}>
                        Related
                    </Grid>
                    <Grid item xs={3} className={classes.rowsTitle}>
                        Length Acquainted
                    </Grid>
                    <Grid item xs={2} className={classes.rowsTitle}>
                        Status
                    </Grid>
                    {
                        comakers.map((data, index) => {
                            return (
                                <React.Fragment key={`comaker-${index}`}>
                                    <Grid item xs={5} className={classes.rowsContent} sx={{ borderLeft: "1px solid #000 !important", textAlign: "left !important" }}>
                                        {`${data?.profile?.first_name} ${data?.profile?.middle_name ?? ""} ${data?.profile?.last_name} ${data?.profile?.suffix ?? ""}`}
                                    </Grid>
                                    <Grid item xs={2} className={classes.rowsContent}>{
                                        data.isRelated ? "Yes" : "No"
                                    }</Grid>
                                    <Grid item xs={3} className={classes.rowsContent}>{
                                        data.length_acquainted
                                    }</Grid>
                                    <Grid item xs={2} className={classes.rowsContent}>{
                                        data.isConfirmed === 1 ? "Confirmed" : "Not Confirmed"
                                    }</Grid>
                                </React.Fragment>
                            )
                        })
                    }
                </Grid>
            </Paper>
        </div>
    )
}
